import React from 'react';
import { Link } from 'gatsby';
import styled from 'styled-components';

// types
import { INode } from '../../types/markdown';

const StyledPost = styled.div`
  & {
    & a > div {
      position: relative;

      & h3 {
        color: #2d3748;
      }

      & ::after {
        content: '';
        position: absolute;
        top: -6px;
        left: 0;
        width: 18px;
        height: 1px;
        transform: scale(0, 1);
        transform-origin: 0% 0%;
        transition: opacity 0.35s, transform 0.35s;
        border-bottom: 1px solid;
        opacity: 0;
      }

      & :hover {
        & h3 {
          color: #000;
        }

        &::after {
          transform: scale(1);
          opacity: 1;
        }
      }
    }
  }
`;

interface Props {
  title: string;
  node: INode;
}

const Post: React.FC<Props> = ({ title, node }) => {
  return (
    <StyledPost className="mb-12">
      <div className="inline-block">
        <Link to={node.fields.slug}>
          <h3 className="text-lg">{title}</h3>
          <p
            className="mt-1 text-gray-600 text-sm"
            dangerouslySetInnerHTML={{
              __html: node.frontmatter.description || node.excerpt,
            }}
          />
          <p className="mt-1 text-gray-500 text-xs">{node.frontmatter.date}</p>
        </Link>
      </div>
    </StyledPost>
  );
};

export default Post;
